.about {
  padding-top: 70px;
  @media screen and (min-width: 1366px) {
    padding-top: 130px;
  }
}

.about-container {
  @media screen and (min-width: 480px) and (max-width: 767px) {
    margin: 0 auto;
    padding: 0;
    width: 440px;
  }
}

.section-title_teacher {
  text-align: left;
  margin-bottom: 30px;

  @media screen and (min-width: 1366px) {
    margin-bottom: 50px;
  }
}

.about-wrap {
  @media screen and (min-width: 768px) {
    display: flex;
  }
}

.about__pic {
  width: 243px;
  height: 306px;

  @media screen and (min-width: 1366px) {
    width: 530px;
    height: 668px;
  }
}

.about-field {
  @media screen and (min-width: 768px) {
    margin-left: auto;
    width: 403px;
  }

  @media screen and (min-width: 1366px) {
    margin-left: 60px;
    width: 642px;
  }
}

.about-field__name {
  margin-top: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid var(--main-text-color);

  font-weight: 800;
  font-size: 20px;
  line-height: 1.2;
  color: var(--deepdark-text-color);

  @media screen and (min-width: 768px) {
    margin-top: 0;
  }

  @media screen and (min-width: 1366px) {
    padding-bottom: 15px;

    font-size: 30px;
    line-height: 1.23;
  }
}

.teacher-portf-list {
  padding-top: 20px;
  padding-bottom: 20px;

  @media screen and (min-width: 768px) {
    padding-bottom: 30px;
  }

  @media screen and (min-width: 1366px) {
    padding-bottom: 43px;
  }
}

.teacher-portf-list__item {
  position: relative;
  margin-bottom: 20px;
  padding-left: 27px;

  font-weight: 600;
  font-size: 14px;
  line-height: 1.21;
  letter-spacing: 0.06em;

  color: var(--deepdark-text-color);

  @media screen and (min-width: 768px) {
    font-size: 12px;
    line-height: 1.25;

    padding-left: 28px;

    color: var(--dark-text-color);
  }

  @media screen and (min-width: 1366px) {
    margin-bottom: 30px;
    padding-left: 59px;

    font-size: 16px;
    line-height: 1.25;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.teacher-portf-list__icon {
  position: absolute;
  top: 0;
  left: 0;
  fill: var(--accent);

  width: 18px;
  height: 14px;

  @media screen and (min-width: 1366px) {
    width: 25px;
    height: 20px;
  }
}

.about-field__separation {
  background-color: var(--accent);
  width: 20px;
  height: 2px;
}

.teacher-descr-list {
  padding-top: 20px;
  padding-bottom: 20px;

  @media screen and (min-width: 768px) {
    padding-top: 30px;
  }

  @media screen and (min-width: 1366px) {
    padding-top: 43px;
    padding-bottom: 22px;
  }
}

.teacher-descr-list__item {
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: 0;

  @media screen and (min-width: 768px) {
    font-size: 10px;
    line-height: 2;

    margin-bottom: 15px;
  }

  @media screen and (min-width: 1366px) {
    font-size: 18px;
    line-height: 1.55;

    margin-bottom: 28px;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.about-field__link {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.67;

  text-decoration-line: underline;
  letter-spacing: 0;

  color: var(--accent);

  @media screen and (min-width: 768px) {
    line-height: 1.66;
  }
  @media screen and (min-width: 1366px) {
    font-size: 18px;
    line-height: 1.55;
  }
}
